
.slide-trans-enter-active {
    -webkit-transition: all .5s;
    transition: all .5s;
}
.slide-trans-enter {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
.slide-trans-old-leave-active {
    -webkit-transition: all .5s;
    transition: all .5s;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
.slide-show {
    position: relative;
    margin: 15px 15px 15px 0;
    width: 100%;
    height: 200px;
    overflow: hidden;
}
.slide-show h2 {
    position: absolute;
    width: 100%;
    height: 100%;
    color: #fff;
    background: #000;
    opacity: .5;
    bottom: 0;
    height: 30px;
    text-align: left;
    padding-left: 15px;
}
.slide-img {
    width: 100%;
    height: auto;
}
.slide-img a{
    width: 100%;
    height: auto;
}
.slide-img img {
    width: 100%;
    height: auto;
    position: absolute;
    top: 0;
}
.slide-pages {
    position: absolute;
    bottom: 10px;
    right: 15px;
}
.slide-pages li {
    display: inline-block;
    padding: 0 10px;
    cursor: pointer;
    color: #fff;
}
.slide-pages li .on {
    text-decoration: underline;
}
